/* ====================
Profile_Sectiion_Start
=======================*/

.Home{
  text-align: center;
  padding: 60px 80px 0;
    /* padding: 60px 40px 0; */
}

#slack{
  display: none;
}


#header_profile {
  position: relative;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 400px;

}


#sub_headeer{
    max-width: 88px;
    height: 88px;
}

.image {
    border-radius: 50%;
  display: block;
  width: 88px;
  height: 88px;
  
}

.overlay{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 90px;
  width: 88px;
  margin : 0 auto;
  opacity: 0;
  transition: .3s ease;
  border-radius: 50%;

  /* background-color: rgb(136, 98, 98); */
  background-color: transparent;
}

#header_profile:hover .overlay {
  opacity: 1;
}

.icon {
  color: white;
  font-size: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}


#avatar_placeholder{
    color: transparent;
    position: relative;
    height: 42px;
    width: 42px;
    margin : 0 auto;


}

#avatar_share_button{


position: absolute;
width: 40px;
height: 40px;
left: 708px;
top: -20px;


background: #FFFFFF;


border: 1px dashed #D0D5DD;
border-radius: 20px;


flex: none;
order: 2;
flex-grow: 0;
z-index: 2;
}


