ul{
    padding: 0 32px;
}

li {
    border: 1px solid #eaecf0;
    border-radius: 8px;
    background: #EAECF0;
    color: #101828;
    
    padding: 20px;
    margin-bottom: 24px;

    list-style-type: none;
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    cursor: pointer;
    
}
li:hover{
 background: #c7c4c486;
}


.sm_section{
    
    display: flex;
    
    justify-content: center;
    padding: 24px 0;
    margin-bottom: 42px;
}

#slack_logo
{
    width:24px;
    margin-right: 24px;
    margin-left: -24px;
}

a{
    text-decoration: none;
    
}



a.tooltip {
    border-bottom: 1px dashed;
    text-decoration: none;
  }
  a.tooltip:hover {
    cursor: help;
    position: relative;
  }
  a.tooltip span {
    display: none;
  }
  a.tooltip:hover span {
    border: #666 2px dotted;
    padding: 5px 20px 5px 5px;
    display: block;
    z-index: 80px;
    background: #e3e3e394;
    left: 100px;
    margin: 15px;
    width: 300px;
    position: absolute;
    top: 15px;
    text-decoration: none;
  }





